import React, { Component } from 'react';
import './App.scss';
import FlightListing from './FlightListing';
import FlightMap from './FlightMap';
import Alerts from './Alerts';
let geolib = require('geolib');

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      planes: [],
      currentLocation: [-33.733534, 151.100924],
      highlightAircraft: null
    }
  }

  componentDidMount(){
    var that = this;
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(function(position) {
        that.setState({centerLocation: [position.coords.latitude, position.coords.longitude] })
      })
    }

    this.interval = setInterval(() => this.loadAircraft(), 1000);
  }

  loadAircraft(){
    var that = this;
    fetch('http://127.0.0.1:8080/data/aircraft_parsed.json', {
      cache: 'no-store'
    })
    .then(function(response) {
      return response.json();
    })
    .then(function(aircraftJson) {
      let aircraftArray = aircraftJson.aircraft;
      aircraftArray.forEach(function(aircraft){
        let distanceTo = null;
        let direction = null;
        if(aircraft.flight.coordinates){
          distanceTo = geolib.getDistance(
            {latitude: that.state.currentLocation[0], longitude: that.state.currentLocation[1]},
            {latitude: aircraft.flight.lat, longitude: aircraft.flight.lon}
          );
          direction = geolib.getCompassDirection(
            {latitude: that.state.currentLocation[0], longitude: that.state.currentLocation[1]},
            {latitude: aircraft.flight.lat, longitude: aircraft.flight.lon}
          ).exact;
        }
        aircraft.userToAircraft = {
          distance: distanceTo,
          direction: direction
        }
      });
      that.setState({planes: aircraftArray});
    }).catch(function(error) {
      console.log('Fetch Request Failed ' + error);
    });
  }

  aircraftMarkerClickHandler(icao) {
    let higlight = icao === this.state.highlightAircraft ? null : icao;
    this.setState({highlightAircraft: higlight })
  };

  render() {
    let that = this;
    let listings = this.state.planes
      .sort(function (a1, a2) {
        return a1.userToAircraft.distance ? (a1.userToAircraft.distance - a2.userToAircraft.distance) : 1;
      }).map(function(aircraft){
        return <FlightListing aircraftMarkerClickHandler={that.aircraftMarkerClickHandler.bind(that)} aircraft={aircraft} key={aircraft.flight.icao} highlight={that.state.highlightAircraft} />
      })

    return (
      <div className="container">
        <div className="map-container">
          <FlightMap
            centerLocation={this.state.currentLocation}
            planes={this.state.planes}
            aircraftMarkerClickHandler={this.aircraftMarkerClickHandler.bind(this)}
            highlight={that.state.highlightAircraft}
          />
        </div>
        <div className="info-pane">
          <Alerts planes={this.state.planes} />
          <div className="flight-listings">
            {listings}
          </div>
        </div>
      </div>
    );
  }

  calculateAlerts(){
    //check for new alerts to trigger
    // check if the alert is new (not already in array)
    //if not new, add to array



    // going over Top alert
    // check aircraft distance, if any 500 meters, call out
  }

}

export default App;
