import React, { Component } from 'react';
import icon from './aircraftatlas.png';
//https://en.wikipedia.org/wiki/List_of_ICAO_aircraft_type_designators
let aircraftMapping = {
  'B77W': 'a1',
  'B738':	'a1',
  'A320': 'a1',
  'A321': 'a1',
  'A319': 'a1',
  'B737': 'a1',
  'E175': 'a1',
  'A20N': 'a1',
  'B739': 'a1',
  'A333': 'a1',
  'DH8D': 'a1',
  'A388': 'a9',
  'C172': 'a19'
}

let atlasMapping = {
  'a1': '-8px -7px',
  'a1Active': '-83px -7px',
  'a2': '-8px -7px',
  'a2Active': '-8px -7px',
  'a3': '-8px -7px',
  'a3Active': '-8px -7px',
  'a4': '-8px -7px',
  'a4Active': '-8px -7px',
  'a5': '-8px -7px',
  'a5Active': '-8px -7px',
  'a6': '-8px -7px',
  'a6Active': '-8px -7px',
  'a7': '-8px -7px',
  'a7Active': '-8px -7px',
  'a8': '-8px -7px',
  'a8Active': '-8px -7px',
  'a9': '-8px -7px', // a380
  'a9Active': '-8px -7px',
  'a10': '-8px -7px',
  'a10Active': '-8px -7px',
  'a11': '-8px -7px',
  'a11Active': '-8px -7px',
  'a12': '-8px -7px',
  'a12Active': '-8px -7px',
  'a13': '-8px -7px',
  'a13Active': '-8px -7px',
  'a14': '-8px -7px', // helicopter
  'a14Active': '-8px -7px',
  'a15': '3px -290px',
  'a15Active': '-8px -7px',
  'a16': '-110px -287px', //glider
  'a16Active': '-8px -7px',
  'a17': '-8px -7px',
  'a17Active': '-8px -7px',
  'a18': '-8px -7px',
  'a18Active': '-8px -7px',
  'a19': '-429px -284px', // light aircraft - single engine
  'a19Active': '-476px -284px',
  'a20': '-8px -7px', // station
  'a20Active': '-8px -7px',
  'a21': '-8px -7px',
  'a21Active': '-8px -7px',
  'a22': '-8px -7px', //vechile
  'a22Active': '-8px -7px',
  'a23': '-8px -7px', //hot air ballon
  'a23Active': ['-8px -7px']
}

class AircraftMarker extends Component{

  render() {
    let track = this.props.aircraft.flight.track || 0;
    let label = this.props.aircraft.flight.flight || this.props.aircraft.flight.icao;
    let markerClassName = 'marker-aircraft';
    if(this.props.highlight === this.props.aircraft.flight.icao){
      markerClassName += ' marker-active';
    }
    return(
      <div className={markerClassName} onClick={() => this.props.aircraftMarkerClickHandler(this.props.aircraft.flight.icao)} style={{
        position: 'absolute',
        width: 40,
        height: 40,
        left: -20,
        top: -20
      }}>
      <div style={{
        width: '100%',
        height: '100%',
        transform: 'rotate('+track+'deg)',
        position: 'relative'
      }}>
        <span style={{
          display: 'block',
          backgroundImage: 'url('+icon+')',
          backgroundSize: '512px',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: this.aircraftAtlasMapping(),
          position: 'absolute',
          left: 0,
          top: 0,
          width: '100%',
          height: '100%',
          cursor: 'pointer'
        }}></span>
      </div>
      <div style={{
          position: 'absolute',
          bottom: '-22px',
          background: '#000000',
          padding: '3px 6px',
          borderRadius: '3px',
          color: '#FFFFFF',
          fontSize: '0.65rem'
        }}>
        {label}
      </div>
    </div>
    );
  }

  aircraftAtlasMapping(){
    let active = this.props.highlight === this.props.aircraft.flight.icao;
    let aircraft = this.props.aircraft.aircraft.type || '';
    let aircraftMappingType = aircraftMapping[aircraft] || 'a1';
    let coordinates = atlasMapping[aircraftMappingType + (active ? 'Active' : '')];
    return coordinates;
  }

}

export default AircraftMarker;
