import React, { Component } from 'react';
import Alert from './Alert';

class Alerts extends Component {

  constructor(props) {
    super(props);
    this.state = {
      alerts: []
    }
  }

  componentWillReceiveProps(nextProps){
    this.addProximityAlerts(nextProps.planes);
    this.addSquawkAlerts(nextProps.planes);
  }

  render() {
    let alerts = this.state.alerts.map(function(alert){
      return <Alert key={alert.key} alert={alert} />
    })

    return(
      <div className="alert-pane">
        {alerts}
      </div>
    );
  }

  addProximityAlerts(planes){
    let that = this;

    planes.forEach(function(aircraft){
      if(aircraft.userToAircraft.distance !== null && aircraft.userToAircraft.distance < 500){
        // aircraft within 500m
        let key = 'proximity-'+aircraft.flight.icao;
        if(that.newAlert(key)){
          let message = `Flight ${aircraft.flight.flight} is ${aircraft.flight.altitude} feet above`;
          that.addAlert(key, message);
        }
      }
    })
  }

  addSquawkAlerts(planes){
    let that = this;

    planes.forEach(function(aircraft){
      if(aircraft.flight.squawk === '7700'){
        let key = 'squawk-'+aircraft.flight.icao;
        if(that.newAlert(key)){
          let message = `Flight ${aircraft.flight.flight} has declared an emergency with squawk 77 hundred`;
          that.addAlert(key, message);
        }
      }
    })
  }

  addAlert(key, message, speak = true){
    this.setState({alerts: [...this.state.alerts, {key: key, message: message}]});
    if(speak){
      this.speakMessage(message);
    }
  }

  async speakMessage(message){
    let msg = new SpeechSynthesisUtterance(message);
    speechSynthesis.speak(msg);
  }

  // Check if alert exists already
  newAlert(key){
    return this.state.alerts.find(function(alert){
      return alert.key === key;
    }) === undefined;
  }
}

export default Alerts;
