import React, { Component } from 'react';
import './App.scss';

import GoogleMap from 'google-map-react';
import AircraftMarker from './AircraftMarker';
import UserLocationMarker from './UserLocationMarker';

class FlightMap extends Component {

  constructor(props) {
    super(props);
    this.state = {
      mapLoaded: false,
    }
    this.allFlightLines = [];
  }

  render() {
    return (
      <GoogleMap
        bootstrapURLKeys={{key: 'AIzaSyAhLbyKUFicvcZaf3jKYIjpAnTZwiVHL5o'}}
        center={this.props.centerLocation}
        zoom={11}
        onGoogleApiLoaded={({ map, maps }) => {
          this.setState({ map: map, maps:maps, mapLoaded: true })

          var imageMapType = new maps.ImageMapType({
          getTileUrl: function(coord, zoom) {
            var url = ['https://tile.openweathermap.org/map/precipitation_new/',zoom,'/',coord.x,'/',coord.y,'.png?appid=79fa7586a49c6c97ac2c7dfcd274edef'].join('');
            return url;
          },
          tileSize: new maps.Size(256, 256),
          isPng: true
        });

        map.overlayMapTypes.push(imageMapType);
        }}
        yesIWantToUseGoogleMapApiInternals>
        {this.props.centerLocation != null &&
          <UserLocationMarker key='user-location' lat={this.props.centerLocation[0]} lng={this.props.centerLocation[1]} />
        }

        {this.state.mapLoaded && this.loadAllFlightPaths()}
        {this.aircraftMarkers()}
      </GoogleMap>
    );
  }

  aircraftMarkers(){
    var that = this;
    var markers = [];
    if (this.props.planes !== undefined) {
      markers = this.props.planes
      .filter(aircraft => {
        return aircraft.flight.coordinates;
      })
      .map(aircraft => {
        return (
          <AircraftMarker highlight={that.props.highlight} aircraftMarkerClickHandler={that.props.aircraftMarkerClickHandler} aircraft={aircraft} key={aircraft.flight.icao} lat={aircraft.flight.lat} lng={aircraft.flight.lon} />
        );
      });
    }
    return markers;
  }

  loadAllFlightPaths(){
    // clear flightline if need be
    if(this.allFlightLines.length > 0){
      this.allFlightLines.forEach(function(line){
        line.setMap(null);
      });
    }
    let that = this;
    let planes = [];
    if(this.props.highlight != null){
      let highlightedPlane = this.props.planes.find(function(plane) {
        return plane.flight.icao === that.props.highlight;
      });
      planes.push(highlightedPlane);
    }else{
      planes = this.props.planes;
    }
    planes.forEach(function(aircraft){
      if(aircraft.waypoints.length > 1){
        let line = new that.state.maps.Polyline({
          geodesic: true,
          strokeColor: '#5087ec',
          strokeOpacity: 1,
          strokeWeight: 2,
          path: aircraft.waypoints
        })
        line.setMap(that.state.map);
        that.allFlightLines.push(line);
      }
    })
  }

}

export default FlightMap;
